import { useState } from "react";
import { EduMgmtimg, Button, Side } from "./styled-component";
import { Container } from "react-bootstrap";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";
import BusinesSarea from "./Contents/Businex/BusinesSarea";
import Eduservices from "./Contents/Eduservices/Eduservices";

const EduMgmt = () => {
  const [clicked, setClicked] = useState(1);

  const handleClick = (e) => setClicked(e);

  const device = useDevice(MediaQuery);

  return (
    <section>
      <Container>
        <EduMgmtimg mobile={device.isMobile}>
          <div>
            <h2>아이리포 교육 운영을 소개합니다</h2>
            <p>
              아이리포 교육에서는 다양한 분야의 경험 많은 전문가들과 함께합니다.
              <br />
              두려워 하지마시고 도전을 하세요!
            </p>
          </div>
        </EduMgmtimg>
        <Side mobile={device.isMobile}>
          <Button clicked={clicked === 2} onClick={() => handleClick(1)}>
            사업 영역
          </Button>
          <Button clicked={clicked === 1} onClick={() => handleClick(2)}>
            교육 서비스
          </Button>
        </Side>
        {clicked === 1 ? <BusinesSarea /> : <Eduservices />}
        <hr />
      </Container>
    </section>
  );
};

export default EduMgmt;
