import ConsentForm from "./components/ConsentForm";
import {
  CallBox,
  Callinput,
  Input,
  Select,
  TextArea,
  Field,
  Button,
} from "./styled-components";

const FieldContainer = ({ children, title, required }) => (
  <Field>
    <p>
      {required && <span>＊</span>}
      {title}
    </p>
    {children}
  </Field>
);

const Call = () => (
  <CallBox>
    <Callinput>
      <div className="Inputname">
        <FieldContainer title="이름" required>
          <Input />
        </FieldContainer>
        <FieldContainer title="전화번호" required>
          <Input
            onKeyPress={(e) => {
              if (
                !/[0-9]/.test(e.key) ||
                /[\u3131-\u3163\uac00-\ud7a3]/g.test(e.key)
              ) {
                e.preventDefault();
              }
            }}
          />
        </FieldContainer>
      </div>

      <FieldContainer title="이메일" required>
        <Input />
      </FieldContainer>
      <FieldContainer title="유입경로">
        <Select>
          <option value="" disabled selected>
            선택해주세요
          </option>
          <option value="naverCafe">네이버 카페</option>
          <option value="instagram">인스타그램</option>
          <option value="onEyeRepo">온아이리포</option>
          <option value="dippleWaffle">디플와플</option>
          <option value="recommendation">지인추천</option>
          <option value="youtube">유튜브</option>
          <option value="blog">블로그</option>
          <option value="sns">SNS</option>
          <option value="search">검색</option>
          <option value="etc">기타</option>
        </Select>
      </FieldContainer>
      <FieldContainer title="문의 내용">
        <TextArea />
      </FieldContainer>
      <ConsentForm />
      <Button>문의하기</Button>
    </Callinput>
  </CallBox>
);

export default Call;
