import React, { useState } from "react";
import {
  ConsentLabel,
  ConsentCheckbox,
} from "./styled-components";

const ConsentForm = () => {
  const [personalInfoConsent, setPersonalInfoConsent] = useState(false);
  const [serviceInfoConsent, setServiceInfoConsent] = useState(false);

  const handlePersonalInfoConsentChange = (e) => {
    setPersonalInfoConsent(e.target.checked);
  };

  const handleServiceInfoConsentChange = (e) => {
    setServiceInfoConsent(e.target.checked);
  };

  return (
    <>
      <ConsentLabel>
        <ConsentCheckbox
          type="checkbox"
          checked={personalInfoConsent}
          onChange={handlePersonalInfoConsentChange}
        />
        [필수] 개인(신용)정보 수집 및 이용에 동의합니다
      </ConsentLabel>
      <ConsentLabel>
        <ConsentCheckbox
          type="checkbox"
          checked={serviceInfoConsent}
          onChange={handleServiceInfoConsentChange}
        />
        [필수] 서비스 제안 및 상담 목적의 정보 수신에 동의합니다
      </ConsentLabel>
    </>
  );
};

export default ConsentForm;
